import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, message, Spin, Checkbox } from 'antd';
import to from 'await-to-js';
import { addCreateRole, getMenuByModuleId, updateRole } from 'services/roleModule';
import { getQueryParams } from 'utils/urlUtil';

@Form.create({ name: 'ForManagementUser' })
class ForManagementUser extends Component {
  state = {
    moduleId: false,
    loading: false,
    initialValue: {
      name: '',
      moduleId: '',
      disabled: false,
    },
  };

  componentDidMount = () => {
    const { moduleId, roleId } = getQueryParams();
    if (roleId) {
      this.getUser(moduleId, roleId);
    }
    this.setIdRole(moduleId);
  };

  setIdRole = value => {
    this.setState({ moduleId: value });
  };
  getUser = async (moduleId, roleId) => {
    const { form } = this.props;
    this.setState({
      roleId,
      loading: true,
    });
    const [error, response] = await to(getMenuByModuleId(roleId, moduleId));
    if (error) return;
    const infoRole = {
      moduleId: response.data.moduleId,
      name: response.data.roleName,
      disabled: response.data.disabled,
    };
    form.setFieldsValue(infoRole);
    this.setState({
      initialValue: infoRole,
      loading: false,
    });
  };

  handleSubmit = e => {
    const { roleId, moduleId } = this.state;
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (!err) {
        if (roleId) {
          const dataEdit = {
            moduleId,
            roleId,
            name: values.name,
            disabled: values.disabled,
          };
          const [error] = await to(updateRole(moduleId, roleId, dataEdit));
          if (error) return;
          message.success('Chỉnh sửa thành công');
          this.props.history.push(`/management-user-role/form/edit?id=${moduleId}`);
        } else {
          const [error] = await to(addCreateRole(moduleId, values));
          if (error) return;
          message.success('Thêm thành công');
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card>
        <Spin spinning={this.state.loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item hasFeedback label="Tên" {...formItemLayout}>
                {getFieldDecorator('name', {
                  initialValue: initialValue.name,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Disable">
                {getFieldDecorator('disabled', {
                  valuePropName: 'checked',
                  initialValue: initialValue.disabled,
                })(<Checkbox />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ForManagementUser {...props} />
  </Page>
));



